.postBlock {
  border: 1px solid;
  display: flex;
  flex-direction: column;
  padding: 10px;
  background-color: lightblue;

  &__title {
    font-size: 19px;
  }

  &__text {
    padding: 5px;
  }

  &__date {
    padding: 5px;
  }

  &__buttons {
    margin-top: 5px;
  }
}

